import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    token: '',
    isLogin: false,
    user: {
      id: 'maple'
    }
  },
  mutations: {
    changeLogin (state, isLogin) {
      state.isLogin = isLogin
    },
    set_token (state, token) {
      state.token = token
      // sessionStorage.token = token
    },
    del_token (state) {
      state.token = ''
      // sessionStorage.removeItem('token')
    }
  },
  actions: {
  },
  getters: {

  },
  modules: {
  }
})

export default store
